.popup__closed {
  visibility: hidden;
  opacity: 0;
  transition: opacity .4s, visibility 0s linear .4s;
  -moz-transition: opacity .4s, visibility 0s linear .4s;
  -webkit-transition: opacity .4s, visibility 0s linear .4s;
  -o-transition: opacity .4s, visibility 0s linear .4s;
  -ms-transition: opacity .4s, visibility 0s linear .4s;
}
